import React from "react";
import TimeStampPresenter from "./TimeStampPresenter";

const TimeStampContainer = ({ title, time }) => {
  time = time.replace("ago", "");
  time = time.replace("minutes", "");
  time = time.replace("a minute", "1");
  time = time.replace("now", "");
  time = time.replace("hours", "");
  time = time.replace("hour", "");
  time = time.replace("an", "");
  time = time.replace("seconds", "");
  time = time.replace("a seconds", "1");

  return <TimeStampPresenter title={title} time={time} />;
};

export default TimeStampContainer;
